<template>
	<div class="userBtnComponent" :style="{marginTop: topNum}" @touchstart="touchDown" @touchend="touchEnd" @click="toUp">
		<div class="userBtn"><span>{{content}}</span></div>
		<div class="bk" v-show="bkShow"></div>
		<div class="rotateBk" v-show="rotateShow"></div>
	</div>
</template>
<script>
    export default {
        data () {
	    	return {
        		bkShow: false
        	}
	    },
	    props:['content','rotateShow','topNum'],
	    methods: {
	        toUp(){
	      		this.$emit('childClick');
	        },
	        touchDown() {
	        	this.bkShow = true;
		    },
		    touchEnd () {
		    	this.bkShow = false;
		    }
	    },
	    filters:{
            
        }
    }
</script>

<style scoped lang="less">
	.userBtnComponent{
		position: relative;
		width: 3.2*2rem;
		height: 0.44*2rem;
		margin: 0 auto;
		.userBtn{
			width: 3.2*2rem;
		    height: 0.44*2rem;
		    line-height: 0.44*2rem;
		    text-align: center;
		    background-color: #3391E8;
		    color: #fff;
		    border-radius: 4px;
		    letter-spacing: 4px;
		    font-size: 0.18*2rem;
		    font-family: PingFangSC-Medium;
			box-shadow: 0px 4px 6px rgba(45,136,219,0.30);
			span{
				line-height: normal;
				padding: 0.2rem 0;
			}
		}
		.bk{
			position: absolute;
			width: 3.2*2rem;
		    height: 0.44*2rem;
		    background-color: #000;
		    opacity: .3;
		    top: 0;
		    left: 0;
		}
		.rotateBk{
			background: url('../../../assets/user/but_busy.png') no-repeat;
			background-size: 100% 100%;
			width: 0.13*2rem;
			height: 0.13*2rem;
			position: absolute;
			top: 0.16*2rem;
			right: 0.76*2rem;
			animation: rotate 3s linear infinite;
		}
		@keyframes rotate{
			from{transform: rotate(0deg)}
			to{transform: rotate(359deg)}
		}
	}
</style>


