<template>
	<div>
		<div class="login_input_block" v-on:touchstart="changeShade" v-on:touchend="changeShade" @click="validate">
			<input type="button" :value="value" class="global_btn" :class="status" />
			<div class="busy busy_btn"  v-show="btnState"></div>
			<div class="global_btn_shade" :class="status" v-show="ifshade"></div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				ifshade: false
			};
		},
		props: ['value', 'status', 'btnState'],
		methods: {
			changeShade() {
				this.ifshade = !this.ifshade
			},
			validate() {
				this.$emit('validateCode')
			}
		}
	}
</script>

<style scoped lang="less">
	@import '../common/css/global_pyt.less';
	
	.login_input_block {
		position: relative;
		display: flex;
		align-items:center;
		.global_btn {
			width: 2.9*2rem;
			height: 0.44*2rem;
			font-size: .18*2rem;
			border-radius: 4px;
			padding: 0;
			text-align: center;
			letter-spacing: 4px;
			border: none;
			box-shadow: 0 4px 6px 0 rgba(45,136,219,0.3);
			margin: 0 auto;
			display: block;
		}
		.global_btn_shade {
			position: absolute;
			top: 0;
			left: 50%;
			transform: translateX(-50%);
			width: 2.9*2rem;
			height: 0.44*2rem;
			background-color: rgba(0, 0, 0, .3);
			transition: all .3s;
		}
		.global_btn.normal {
			background-color: #3391E8;
			color: #fff;
		}
		.register_btn {
			width: 3.2*2rem;
		}
		.busy {
			background-image: url(../assets/But_busy.png);
			background-size: 100%;
			background-repeat: no-repeat;
			width: .15*2rem;
			height: .15*2rem;
			position: absolute;
			right: 24%;
		}
	}	
</style>



